@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#title {
    color: #f15f25;
    letter-spacing: 2px;
    font-size: 1.6rem;
    font-family: 'Fredoka One', cursive;
}

.rc_nav {
    overflow: hidden;
    background-color: #ffffff;
    text-align: center;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 6;
}

.nav_link {
    letter-spacing: 1px;
    margin: 0 5px;
    display: inline-block;
    color: rgb(51, 51, 51);
    padding: 10px 22px;
    text-decoration: none;
    font-size: 18px;
    transition: background 0.3s linear;
    z-index: 9;
    text-transform: uppercase;
}

.nav_link:hover {
    transition: 0.7;
}

@media screen and (max-width: 600px) {
    .nav_link {
        padding: 7px 10px;
        font-size: 0.7rem;
    }

    #title {
        font-size: 1.5rem;
    }

}

.active_class {
    background-color: #F3F1F5;
    color: #A4AFCD;
    border-radius: 10px 10px 0 0;
}

#webpage {
    font-family: 'Noto Sans', sans-serif;
    background-color: #F3F1F5;
    height: 100vh;
    width: 100vw;
}

#webpage_body {
    padding-top: 8rem;
    margin: auto;
    width: 75%;
}

.error_page {
    padding-top: 10rem;
}

/* Home Page Body Section */


/* Social Media Section */
.icon-bar {
    position: fixed;
    top: 60%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.icon-bar a {
    display: block;
    text-align: center;
    padding: 16px;
    transition: all 0.3s ease;
    color: #000;
    font-size: 20px;
}

.icon-bar a:hover {
    color: #ff0bb6;
}

/* Portfolio Section */
#portfolio_webpage {
    font-family: 'Noto Sans', sans-serif;
    padding-top: 8rem;
    background-color: #F3F1F5;
}

.portfolio_body {
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
}

.portfolio_card {
    margin: 1rem;
    width: 30%;
    height: 400px;
    border: 1px solid #ccc;
    margin-bottom: 50px;
    text-align: center;
}



@media screen and (max-width:1000px) {
    .portfolio_card {
        width: 65%;
    }

}


@media screen and (max-width:620px) {
    .portfolio_card {
        width: 80%;
        margin: 0.9rem;
        height: 350px;
    }

    .title {
        font-size: 1rem;
    }

    .btn {
        font-size: small;
    }

    .portfolio_body {
        width: 100%;
    }

}
